import React from 'react';
import {motion} from 'framer-motion';
import './index.css';

const containerVariants = {
    visible: {
        transition: {
            staggerChildren: 1,
        }
    }
}

const childVariants = {
    hidden: {
        scale: 0,
    },
    visible: {
        scale: 1,
        transition: {
            duration: 1,
        },
    },
    exit: {
        opacity: 0,
        transition: {
            delay: 3,
            duration: 4,
        }
    }
}
const childsVariants = {
    hidden: {
        y:'110vh',
    },
    visible: {
        y:0,
        transition: {
            duration: 3,
        },
    },
}

function Showcase(props) {
    return (
        <motion.div className="container-showcase" variants={containerVariants} initial="hidden" animate="visible" exit="exit">
            <motion.img variants={childVariants} src="assets/img1.webp" width="300px" alt="logo" />
            <motion.img variants={childVariants} src="assets/img2.webp" width="300px" alt="logo" />
            <motion.img variants={childVariants} src="assets/img3.webp" width="300px" alt="logo" />
            <motion.div className="container-eventby" variants={childsVariants}>
                <motion.h4>Events by </motion.h4>
                <motion.img src="assets/ii.png" className="eventby-img" alt="logo" layoutId="main-home-heading"></motion.img>
            </motion.div>
            <motion.img variants={childVariants} src="assets/img4.webp" width="300px" alt="logo" />
            <motion.img variants={childVariants} src="assets/img5.webp" width="300px" alt="logo" />
            <motion.img variants={childVariants} src="assets/img6.webp" width="300px" alt="logo" />
        </motion.div>
    );
}

export default Showcase;